import React,{useEffect, useState} from 'react'
import NoticeApi from '../../../../Api/NoticeApi'
import ProApi from '../../../../Api/ProApi'
import Confirm from '../../../../Modal/Component/Confirm'
import NumberUtils from '../../../../Utils/NumberUtils'
import Alert from '../../../../Modal/Component/Alert'

const DetailProcurement = (props) => {

	const [data, setData] = useState({})

	useEffect(()=> {
		let params = { no: props.detail }
		ProApi.detailPro(params)
			.then(res => {
				setData(res.data.data)
			})
	},[props.detail])

	const deleteNotice = () => {
		let no = [props.detail]
		ProApi.deletePro(no)
			.then(()=> {
				props.setWriteMode(0)
			})
	}

	const subject = (d,s) => {
		switch (s) {
			case 'main' : {
				if (d === 'PRO' || d === 'MAS') {
					return `${d === 'PRO' ? '' : 'MAS'} / `
				} return 'SW /  '
			}
			case 'sub' : {
				if (d === 'PRO' || d === 'MAS') {
					return `${d === 'PRO' ? '' : 'MAS'} / `
				} return 'SW /  '
			}
		}
	}

	const onUpdate = () => {
		window.popupManager.open(Confirm, {
			contents: '?',
			onConfirm: () => {
				props.setWriteMode(3)
				props.setUpdateNumber(props.detail)
			}
		})
	}


	return (
		<>
			<div className="title_box clear">
				<h2 className="title left"> </h2>

				<ul className="right">
					<li>HOME</li>
					<li> </li>
					<li> </li>
				</ul>
			</div>

			<div className="table_wrap">
				<table className="type2 notice_detail txt_content">
					<colgroup>
						<col width="259px"/>
						<col width="*"/>
						<col width="259px"/>
						<col width="*"/>
					</colgroup>
					<tbody>
					<tr>
						<th>/</th>
						<td>{subject(data.division, data.subject)}</td>
						<th></th>
						<td>{data.ino}</td>
					</tr>
					<tr>
						<th></th>
						<td>{data.product_name}</td>
						<th></th>
						<td>{data.model_name}</td>
					</tr>
					<tr>
						<th></th>
						<td>{data.equipment}</td>
						<th>()</th>
						<td>{data.price}</td>
					</tr>
					<tr>
						<th>(/)</th>
						<td colSpan="3">{data.product_standard}</td>
					</tr>
					</tbody>
				</table>

				<div className="btn_center martop_20">
					<button type="button" className="blue_btn w_100 confirm_btn" onClick={()=>onUpdate()}></button>
					<button type="button" className="white_btn w_100" onClick={()=>props.setWriteMode(0)}></button>
				</div>
			</div>
		</>
	)
}

export default DetailProcurement
